





















import Vue from 'vue'
import formElement from './element/index.vue'

export default Vue.extend({
    name: 'iForm',
    components: { formElement },
    props: {
        formItems: {
            type: Array,
            default: function () {
                return []
            }
        },
        model: {
            type: Object,
            default: null
        },
        columns: {
            type: Number,
            default: 1
        }
    },
    computed: {
        modelValue () {
            if (this.model) {
                return this.model
            }
            if (this.formItems.length > 0) {
                const model = {}
                this.formItems.forEach(item => {
                    model[item.key] = item.value || ''
                })
                return model
            }
            return null
        }
    }
})
