



















































import Vue from 'vue'
import mixinMethods from '../mixins/methods'
import RenderComponent from './RenderComponent.vue'
import RenderCustomComponent from './RenderCustomComponent.vue'

export default Vue.extend({
    name: 'CustomColumn',
    components: {
      RenderComponent,
      RenderCustomComponent
    },
    props: {
        column: {
            type: Object,
            default: null
        }
    },
    mixins: [ mixinMethods ]
})
